import MotionWrapper from "@/components/ui/MotionWrapper";
import Button from "@/components/ui/Button";
import Link from "next/link";
import {motion} from "framer-motion";

export default function  About () {
    return (
        <section id="about" className="py-24 bg-muted/30">
            <div className="container px-4 md:px-6">
                <div className="grid gap-6 lg:grid-cols-2 lg:gap-12">

                    <motion.div
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}>
                        <div>Обо мне</div>
                        <h2>Фронтенд разработчик с 5-летним опытом</h2>
                        <p>
                            Привет! Я Александр, фронтенд разработчик из Москвы. Я специализируюсь на создании современных
                            веб-приложений с использованием React, Next.js и TypeScript.
                        </p>
                        <p>
                            Мой путь в разработке начался 5 лет назад, и с тех пор я работал над различными проектами: от небольших
                            лендингов до крупных корпоративных приложений. Я стремлюсь создавать интуитивно понятные, доступные и
                            производительные интерфейсы.
                        </p>
                        <div>
                            <Button>
                                <Link href="#"></Link>
                            </Button>
                        </div>
                    </motion.div>
                </div>
            </div>
        </section>
    )
}
